@import "zero-style.scss";
@import "vars.scss";
@import "fonts.scss";

body {
  background: url("https://image.freepik.com/free-photo/pastel-watercolor-texture-background_1083-168.jpg?1") center /cover;
  padding-top: 150px;
}

.container {
  width: 100%;
  max-width: 400px;
  margin: 0 auto;
}

.constructor {
  padding: 15px;
}

.constructor,
.list {
  background-color: #94d6ba;
  border-radius: 15px;
  margin: 5px 0;
}

.list {
  padding: 15px 0;
}

.constructor__title,
.list__title {
  text-align: center;
  font-size: 22px;
  font-weight: 700;
}

.list__title {
  font-size: 18px;
}

.constructotr__input {
  padding: 7px 14px;
  margin: 20px 0 15px;
  width: 100%;
  border-radius: 15px;
  font-size: 16px;

  &:focus::placeholder {
    color: transparent;
  }
}

.constructor__btn {
  padding: 7px 14px;
  width: 100%;
  border-radius: 15px;
  font-family: inherit;
  color: white;
  background-color: #4b6154;
  box-shadow: 0 3px darken(#4b6154, 15%);
  transition: all .1s ease-in-out;

  &:hover {
    background-color: darken(#4b6154, 5%);
  }

  &:active {
    transform: translateY(3px);
    box-shadow: 0 0 darken(#4b6154, 15%);
  }
}

.list__item {
  font-size: 16px;
  display: flex;
  color: #4b6154;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #64ac8f;
  padding: 5px 15px;
  transition: all .1s ease-in-out;
  cursor: pointer;

  &:hover {
    background-color: #e7f5dc;
  }

  &.checked {
    background-color: #e7f5dc;
    color: rgb(154, 185, 166);
    text-decoration: line-through;
  }
}

.list__item__content {
  flex: 1 1 100%;
}

.delete {
  width: 15px;
  margin-right: 15px;
}